export default {
	data() {
		return {
			showModal: false
		};
	},
	props: {
		// title
		modalTitle: {
			type: String,
			default: ''
		},
		// 是否显示footer
		showButton: {
			type: Boolean,
			default: true
		},
		// 是否显示蒙层
		shadow: {
			type: Boolean,
			default: true
		},
		// 点击蒙层是否关闭模态框
		clickMask: {
			type: Boolean,
			default: true
		},
		// 确定按钮文字
		sureText: {
			type: String,
			default: '确定'
		},
		// 取消按钮文字
		cancelText: {
			type: String,
			default: '取消'
		},
		// 是否显示确定按钮
		sureButton: {
			type: Boolean,
			default: true
		},
		// 是否显示取消按钮
		cancelButton: {
			type: Boolean,
			default: true
		}
	},
	methods: {
		close () {
			this.$emit('cancelModal');
			this.closeModal();
		},
		confirm () {
			this.$emit('confirmModal');
			this.closeModal();
		},
		closeModal () {
			this.showModal = false;
		},
		show () {
			this.showModal = true;
		}
	}
}
