		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		this.tabCurrentIndex = +options.state;
		console.log(this.tabCurrentIndex,options.state)
















		if(+options.state == 0){
			this.loadData('tabChange','全部')
