	methods: {
		//获取订单列表
		loadData(source,status){
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			
			if(source === 'tabChange' && navItem.loaded === true){
				//tab切换只有第一次需要加载数据
				return;
			}
			if(navItem.loadingType === 'loading'){
				this.getDate(status)
				//防止重复加载
				return;
			}
		}, 
		
		
		setData(e){
			console.log(e)
			let pages = getCurrentPages();
			let currPage = pages[pages.length-1];
			// currPage.data.isDoRefresh = e.isDoRefresh
			if (e.isDoRefresh == true){
				e.isDoRefresh = false;
				if(this.tabCurrentIndex === 0){
					this.getDate('全部');
				}else if(this.tabCurrentIndex === 4){
					this.getDate('待评价');
				}
			}else{
			   //不用刷新
			}
		},
		async getDate(status){
			this.orderList = []
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			const response = await uniRequest({
				url:'/orders/info/?status='+status,
				method:'get',
				headers:{
					Authorization:'JWT '+uni.getStorageSync('userInfo').token
				},
			}).then(response=>{
				if(response.status === 200){
					console.log(response.data.order_id)
					if(response.data.order_id.goods === undefined){
						navItem.orderList = []
					}
					const order_id = response.data.order_id;//所有订单对象
					for (var key in order_id) {
						this.orderList.push(order_id[key]);
					}
					setTimeout(()=>{
						let orderList = this.orderList.filter(item=>{
							//添加不同状态下订单的表现形式
							if(item.status === '全部'){
								item.state = 0
							}else if(item.status === '待支付'){
								item.state = 1
							}else if(item.status === '待发货'){
								item.state = 2
							}else if(item.status === '待收货'){
								item.state = 3
							}else if(item.status === '待评价'){
								item.state = 4
							}else if(item.status === '已完成'){
								item.state = 5
							}else if(item.status === '已取消'){
								item.state = 9
							}
							
							item = Object.assign(item, this.orderStateExp(item.state));
							//演示数据所以自己进行状态筛选
							if(state === 0){
								//0为全部订单
								return item;
							}
							return item.state
						});
						orderList.forEach(item=>{
							navItem.orderList.push(item);
						})
						//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
						this.$set(navItem, 'loaded', true);
						//判断是否还有数据， 有改为 more， 没有改为noMore 
						navItem.loadingType = 'nomore';
						
					}, 10);
				}
			}).catch(error=>{
				console.log(error)
			})
		},
		
		//swiper 切换
		changeTab(e){
			this.tabCurrentIndex = e.target.current;
			if(this.tabCurrentIndex === 0){
				this.getDate('全部')
			}else if(this.tabCurrentIndex === 1){
				this.getDate('待支付')
			}else if(this.tabCurrentIndex === 2){
				this.getDate('待发货')
			}else if(this.tabCurrentIndex === 3){
				this.getDate('待收货')
			}else if(this.tabCurrentIndex === 4){
				this.getDate('待评价')
			}else if(this.tabCurrentIndex === 5){
				this.getDate('已完成')
			}
		},
		//顶部tab点击
		tabClick(index){
			console.log(index)
			this.tabCurrentIndex = index;
		},
		//立即支付
		async payOrder(item){
			console.log(item.pay_method_id)
			this.orderId = item.order_id
			console.log(this.code)
			if(!this.code){  // 普通用户
				 if(item.pay_method_id === 2){ // 支付宝支付
					 const res = await uniRequest({
					 	url: '/orders/'+this.orderId+'/payment/?mobile=1',
					 	method: 'get',
					 	headers: {
					 		Authorization: 'JWT ' + uni.getStorageSync('userInfo').token
					 	},
					 }).then(res => {
					 	console.log(res.data)






					 }).catch(error => {
					 	console.log(error.data)
					 })
					
				}else if(item.pay_method_id === 8){  // 微信支付
					uniRequest({
						url: '/wechat/h5/orders/'+this.orderId+'/payment/',
						method: 'get',
						headers: {
							Authorization: 'JWT ' + uni.getStorageSync('userInfo').token
						},
					}).then(res => {
						console.log(res.data)






					}).catch(error => {
						console.log(error.data)
					})
				}
			}else{
				if(item.pay_method_id === 5){  // 央采 - 支付宝支付
					uniRequest({
						url: '/orders/'+this.orderId+'/payment/?mobile=1',
						method: 'get',
						headers: {
							Authorization: 'JWT ' + uni.getStorageSync('userInfo').token
						},
					}).then(res => {
						console.log(res.data)






					}).catch(error => {
						console.log(error.data)
					})
					
					
					/*






















					*/
				}
			}
			
		},
		
		//删除订单
		deleteOrder(index){
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(()=>{
				this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},
		// 退货/换货
		replaceGoods(item){
			console.log(item)
			uni.navigateTo({
