			console.log(order_id)
			
			console.log(item)
			uni.navigateTo({
				url:'/pages/order/lookDetails/lookDetails?item='+JSON.stringify(item)+'&order_id='+order_id
			})
		},
		// 物流信息
		async lookViewOrder(item){
			uni.navigateTo({
				url:'/pages/order/trackInfo/trackInfo?order_id='+item.order_id
			})
			console.log(item)
			
		},
		// 确认收货
		async confirmOrder(item){
			console.log(item)
			uni.showLoading({
				title: '请稍后'
			})
			const response = await uniRequest({
				url:'orders/update/status/',
				method:'post',
				data:{
					order_id:item.order_id
				},
				headers:{
					Authorization:'JWT '+uni.getStorageSync('userInfo').token
				},
			}).then(res=>{
				if(res.status === 200){
					console.log(res)
					if(res.data.order_id === item.order_id){
						setTimeout(()=>{
							let {stateTip, stateTipColor} = this.orderStateExp(4);
							item = Object.assign(item, {
								state: 4,
								stateTip, 
								stateTipColor
							})
							if(this.tabCurrentIndex === 3){
								/* let list = this.navList[this.tabCurrentIndex].orderList;
								let index = list.findIndex(val=>val.id === item.id);
								let navItem = this.navList[index];
								console.log(index)
								index !== -1 && list.splice(index, 1); */
								this.getDate('待收货')
								this.$api.msg('收货成功')
								uni.hideLoading();
							}else if(this.tabCurrentIndex === 0){
								this.getDate('全部')
								//取消订单后删除待付款中该项
								this.$api.msg('收货成功')
								uni.hideLoading();
							}
							
						}, 600)
						
					}
				}
				
			}).catch(error=>{
				
			})
		},
		
		//取消订单
		async cancelOrder(item){
			console.log(item)
			uni.showLoading({
				title: '请稍后'
			})
			const response = await uniRequest({
				url:'/orders/cancel/?order_id='+item.order_id,
				method:'get',
				headers:{
					Authorization:'JWT '+uni.getStorageSync('userInfo').token
				},
			}).then(res=>{
				if(res.data.massage){
					this.$api.msg(res.data.massage)
					if(res.data.massage === '该订单不支持直接取消'){
						return
					}
					if(this.tabCurrentIndex === 0){
						this.getDate('全部')
					}else{
						setTimeout(()=>{
							let {stateTip, stateTipColor} = this.orderStateExp(9);
							item = Object.assign(item, {
								state: 9,
								stateTip, 
								stateTipColor
							})
							//取消订单后删除待付款中该项
							if(this.tabCurrentIndex === 1){
								this.getDate('待支付')
							}else{
								this.getDate('待发货')
							}
							/* let list = this.navList[this.tabCurrentIndex].orderList;
							let index = list.findIndex(val=>val.id === item.id);
							console.log(index)
							index !== -1 && list.splice(index, 1); */
							uni.hideLoading();
						}, 600)
					}
					
					
				}
				
			}).catch(error=>{
				
			})
			
		},
		// 评价订单
		accessOrder(item,orderId){
			item.orderId = orderId
			uni.navigateTo({
				url:'/pages/order/assess/assess?item='+JSON.stringify(item)
			})
		},

		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch(+state){
				case 1:
					stateTip = '待付款'; break;
				case 2:
					stateTip = '待发货'; break;
				case 3:
					stateTip = '待收货'; break;
				case 4:
					stateTip = '待评价'; break;
				case 5:
					stateTip = '已完成'; break;
				case 9:
					stateTip = '订单已关闭'; 
					stateTipColor = '#909399';
					break;
					
				//更多自定义
			}
			return {stateTip, stateTipColor};
		}
	},
	
